import React from 'react'
import { Route, Switch, Redirect } from 'react-router-dom';
import AddClient from './add-clients'
import EditClient from './edit-clients'
import ClientsList from './clients-list'

const Clients = ({match, systemData}) => {
	return (
		<Switch>
			<Redirect exact from={`${match}`} to={`${match}/list`} />
			<Route path={`${match}/add-client`} render={ () => (<AddClient systemData={systemData}/>) } />
			<Route path={`${match}/edit-client/:id`} component={EditClient} />			
			<Route path={`${match}/list`} render={ () => (<ClientsList systemData={systemData}/>) } />
		</Switch>
	)
}

export default Clients

